 body{
   background-color: #9266ff;
 }
 .App{
   background-color: #1cf340ad;
   min-height: 500px;
   width:400px;
   margin:40px auto ;
   padding: 20px;
 }
 #to-do-form input{
   background-color: rgb(13, 182, 182);
   border:0;
    width:200px;
    height: 50px;
    padding:0 20px;
    margin:20px; 
    font-size: 18px;
    border-radius: 10px;
    color:#fff;

 }
 #to-do-form input::placeholder
 {
   color:rgb(243, 13, 13);
   font-size:18px ;
 }
 #to-do-form input:focus{
   outline:none;
 }
 #to-do-form button
 {
   height: 50px;
   width: 80px;
   border-radius: 10px;
   border:0;
   background-color: rgba(247, 247, 9, 0.801);
   cursor: pointer;
   font-weight: bolder;
   color:white;
   
 }