.list{
    color: white;
    width: 300px;
    background-color: rgba(29, 26, 219, 0.712);
    height:50px;
    margin:20px auto;
    padding: 10px ;
}
.list p{
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faicons{
    margin:0 5px;
    font-size: 15px;

}
.list p input{
    background-color: transparent;
    border: 0;
    color:white;
    font-size: 18px;

}
.list p input:focus{
    outline: none;
}